/*******************************************************************************
* Bild Slider
*******************************************************************************/
.bild-slider-block {
    position: relative;
    overflow: hidden;

    .bild-slider-init {
        position: relative;

        .owl-stage-outer {
            border-top: 5px solid $brand-color;
        }
    }

    .slider-counter {
        position: absolute;
        top: 14px;
        right: 18px;
    }
}

.bild-slider {
    .fadeIn {
        -webkit-animation: scaleUpFrom 0.5s ease both;
        -moz-animation: scaleUpFrom 0.5s ease both;
        -ms-animation: scaleUpFrom 0.5s ease both;
        animation: scaleUpFrom 0.5s ease both;
    }
    
    .fadeOut {
        -webkit-animation: scaleUpTo 0.5s ease both;
        -moz-animation: scaleUpTo 0.5s ease both;
        -ms-animation: scaleUpTo 0.5s ease both;
        animation: scaleUpTo 0.5s ease both;
    }
}
@-webkit-keyframes scaleUpFrom {
    from { opacity: 0; -webkit-transform: scale(1.1); }
}
@-moz-keyframes scaleUpFrom {
    from { opacity: 0; -moz-transform: scale(1.1); }
}
@-ms-keyframes scaleUpFrom {
    from { opacity: 0; -ms-transform: scale(1.1); }
}
@keyframes scaleUpFrom {
    from { opacity: 0; transform: scale(1.1); }
}

@-webkit-keyframes scaleUpTo {
    to { opacity: 0; -webkit-transform: scale(1.1); }
}
@-moz-keyframes scaleUpTo {
    to { opacity: 0; -moz-transform: scale(1.1); }
}
@-ms-keyframes scaleUpTo {
    to { opacity: 0; -ms-transform: scale(1.1); }
}
@keyframes scaleUpTo {
    to { opacity: 0; transform: scale(1.1); }
}

@include media-breakpoint-up(md) { 
    .bild-slider {
        .bild-slider-block {
            .slider-counter {
                top: 75px;
            }
        }
    }

    .bild-slider-init {
        padding-top: 62px;
    }
}

@include media-breakpoint-up(lg) { 
    .bild-slider {
        .container-fluid {
            .slider-counter,
            .owl-nav {
                max-width: 1140px;
                width: 100%;
                margin-right: 0;
                text-align: right;
                display: inline-block;
                left: 50%;
                transform: translateX(-50%);
                right: auto;
            }
            .slider-counter {
                left: 49.8%;
            }
        }
    }
}


/* Landscape */
@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) { 
    .bild-slider-init{
        .bild-slide{
            min-height:415px;
        }
    }
}