/*******************************************************************************
* Spacing Element
*******************************************************************************/
.spacing-element {
    height: 80px;
    &.spacing-element--medium {
        height: 60px;
    }
    &.spacing-element--small {
        height: 40px;
    }
    &.spacing-element--bg-yellow {
        background-color: $brand-color;
    }
}

@include media-breakpoint-up(xl) {
    .spacing-element {
        height: 100px;
        &.spacing-element--medium {
            height: 80px;
        }
        &.spacing-element--small {
            height: 60px;
        }
    }
}

@include media-breakpoint-up(xxl) {
    .spacing-element {
        height: 120px;
        &.spacing-element--medium {
            height: 100px;
        }
        &.spacing-element--small {
            height: 80px;
        }
    }
}
