/*******************************************************************************
* Footer
*******************************************************************************/
footer.page {
    background-color: $body-color;
    padding: 50px 15px 12px 15px;
    color: #fff;
}
.footer-logo {
    text-align: right;
    position: relative;
    margin-right: 13px;
    margin-top: 10px;
    span {
        font-family: $font-secondary;
        font-size:24px;
        line-height: 31px;
        width: 88px;
        word-break: break-all;
        @include inline-block;
    }
    svg {
        position: absolute;
        right: -14px;
        top: -6px;
    }
}

.footer-main-links {
    margin-bottom: 60px;

    a {
        display: table;
        font-family: $font-secondary;
        @extend %fontsize26;
        color: $brand-color;
        @include transition(all, 0.2s, ease, 0s);
        
        &:hover {
            color: #fff;
        }
        
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
}

.footer-bottom { 
    color: $brand-color-gray;
    a {
        &:hover {
            color: #fff;
        }
    }
}

.footer-blog-link {
    margin-bottom: 44px;
    a {
        line-height: 35px;
        font-weight: $bold-weight;
        letter-spacing: 5px;
        text-transform: uppercase;
        display: table;
        @extend %fontsize21;

        &:not(:last-child) {
            margin-bottom: 6px;
        }
    }
}

.footer-quick-links {
    margin-bottom: 25px;
    ul {
        margin-bottom: 12px;
    }
    li {
        display: table;
        a {
            @extend %fontsize12;
            text-transform: uppercase;
            font-weight: $bold-weight;
            letter-spacing: 2.5px;
        }
    }
}

.footer-social-links {
    li {
        @include inline-block();

        &:not(:first-child) {
            margin-left: 52px;
        }

        a {
            font-size: 25px;
        }
    }
}

// Goto Top button Style
.goto-top{
    border-radius: 50%;
    background-color: $brand-color-secondary;
    width: 40px;
    height: 40px;
    position: fixed;
    right: 30px;
    bottom: 20px;
    text-align: center;
    opacity: 0;
    z-index: 9;
    box-shadow: 0 0 9px rgba(0,0,0,0.3);
    &.visible {
        opacity: 1;
    }
    img{
        margin: 10px;
        height: 20px;
        @include rotate(-90deg);
    }
}

@include media-breakpoint-up(md){
    .footer-top {
        margin-bottom: 68px;
        @include flexbox();
        @include justify-content(space-between);
        @include align-items(center);
        @include flex-direction(row-reverse);
    }
    .footer-logo { 
        span {
            font-size: 31px;
            width: auto;
            word-break: normal;
        }
    }
    .footer-bottom {
        @include flexbox();
        @include justify-content(space-between);
    }
    .footer-main-links { 
        margin-bottom: 0;
        a {
            @include inline-block;
            margin-bottom: 0;

            &:not(:last-child) {
                margin-right: 15px;
                margin-bottom: 0;
            }
        }
    }
    .footer-quick-links {
        ul {
            margin-bottom: 0;
            li {
                @include inline-block;
                &:not(:first-child) {
                    a {
                        padding-left: 10px;
                        position: relative;
                        &::after {
                            content: '|';
                            position: absolute;
                            left: -1px;
                            top: 3px;
                            font-size: 14px;
                            line-height: 16px;
                        }
                    }
                }
            }
        }
    }
    .footer-blog-link {
        margin-bottom: 0;
    }
    .footer-social-links {
        text-align: right;
        ul {
            li {
                &:not(:first-child) {
                    margin-left: 34px;
                }
            }
        }
    }
    .footer-bottom-links {
        @include flexbox();
        @include flex-wrap(wrap);
        @include flex-direction(column-reverse);
        @include justify-content(flex-end);
    }
    footer.page{
        padding: 50px 0 10px;
    }
}