/*******************************************************************************
* Bilder
*******************************************************************************/
.bilder {
    overflow-x: hidden;

    .container-fluid {
        padding-left: 0;
        padding-right: 0;
    }

    img, source {
        width: 100%;
        margin-bottom: 20px;
    }
}

.slider-counter {
    font-size: 17px;
    line-height: 20px;
    font-weight: $bold-weight;
    color: $brand-color;
    letter-spacing: 4px;
}

@include media-breakpoint-up(md) { 
    .slider-counter {
        font-size: 15px;
        line-height: 25px;
    }
}