/*******************************************************************************
* Theme Slider
*******************************************************************************/
.theme-slider {
    position: relative;
    > h2 {
        font-weight: $regular-weight;
        margin-bottom: 15px;
    }
}

.single-theme-slide {
    max-width: 100%;
}

.theme-slider-thumb {
    position: relative;
    overflow: hidden;
    z-index: 1;

    &:hover,
    &:focus {
        cursor: default;
    }
}

.theme-slider-counter {
    font-size: 17px;
    line-height: 20px;
    font-weight: $bold-weight;
    color: $brand-color;
    letter-spacing: 4px;
    position: absolute;
    top: 88px;
    right: 17px;
    @include inline-block();
}

.theme-slider-image {
    height: 375px;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}

@media (max-width: 767px)  {
    .owl-item:not(:first-child) {
        .theme-slider-tile-hover.theme-slider-tile {
            .theme-slider-thumb-title {
                opacity: 0;
                transition: none;
            }
        
            &::before {
                opacity: 0;
                right: -100%;
                transition: none;
            }
        
            &::after {
                left: -100%;
                opacity: 0;
                transition: none;
            }
        }    
    }
}

.owl-item:nth-child(n) .theme-slider-tile-hover.theme-slider-tile-update,
.owl-item.active .theme-slider-tile-hover.theme-slider-tile-update,
.firstActiveItem  .theme-slider-tile-update.theme-slider-tile-hover {
    .theme-slider-thumb-title {
        opacity: 1;
        @include transition(all, 0.5s, ease, 0s);
    }

    &::before {
        opacity: 1;
        right: 0;
        @include transition(all, 0.5s, ease, 0s);
    }

    &::after {
        left: 18%;
        opacity: 0.8;
        @include transition(all, 0.5s, ease, 0s);
    }
}

.theme-slider-tile-hover.theme-slider-tile {
    .theme-slider-thumb-title {
        opacity: 1;
    }

    &::before {
        right: 0;   
    }

    &::after {
        left: 18%;
        opacity: 0.8;
    }
}

.theme-slider-tile {
    position: relative;
    margin-bottom: 34px;
    z-index: -1;

    &::before {
        content: '';
        width: 100%;
        height: 5px;
        background-color: $brand-color;
        position: absolute;
        top: 0;
        right: -100%;
        @include transition(all, 0.5s, ease, 0s);
    }

    &::after {
        content: '';
        background-color: $brand-color;
        position: absolute;
        left: -100%;
        top: 64%;
        width: 210px;
        height: 209px;
        border-top-left-radius: 57px;
        opacity: 0;
        @include rotate (253deg);
        @include transition(all, 0.5s, ease, 0s);
    }
}

.theme-slider-thumb-title {
    opacity: 0;
    position: absolute;
    left: 0;
    bottom: 25px;
    z-index: 1;
    padding: 0 30px;
    @include transition(opacity, 0.5s, ease,  0s);
    
    h2 {
        font-weight: $regular-weight;
        @extend %fontsize37;
        color: #fff;
    }
}

.theme-slider-content {
    padding: 0 30px;

    p {
        color: $brand-color-secondary;
    }

    .external-link {
        margin-top: 16px;
    }
}

.bg-yellow {
    .theme-slider-tile { 
        &::after,
        &::before {
            background-color: #fff;
        }

        h2 {
            color: $brand-color;
        }
    }
}

@include media-breakpoint-up(sm) {
    .single-theme-slide {
        max-width: 50%;
    }
    .theme-slider-init {
        .owl-nav {
            right: 25px;
            @include inline-block();
        }
    }
    .theme-slider-tile {
        h2 {
            font-size: 28px;
            line-height: 35px;
        } 

        &::after {
            top: 74%;
            width: 180px;
            height: 179px;
        }
    } 
    .theme-slider-init {
        padding: 62px 25px;
    }
    .theme-slider-content {
        padding: 0;
    }
    .theme-slider-counter {
        display: none;
    }
}

@include media-breakpoint-up(md) {
    .theme-slider-tile {
        h2 {
            font-size: 35px;
            line-height: 43px;
        } 
        &::after {
            top: 64%;
            width: 210px;
            height: 209px;
        }
    } 
    .theme-slider-image {
        height: 353px;
    }
    .theme-slider-tile { 
        margin-bottom: 53px;
    }
    .theme-slider-content {
        .external-link {
            margin-top: 30px;
        }
    }
}
