    .owl-item:nth-child(5).active .theme-slider-tile-change.theme-slider-tile-hover,
    .owl-item:nth-child(6).active .theme-slider-tile-change.theme-slider-tile-hover,
    .owl-item:nth-child(4).lastActiveItem .theme-slider-tile-change.theme-slider-tile-hover,
    .owl-item:nth-child(5).lastActiveItem .theme-slider-tile-change.theme-slider-tile-hover,
