/*******************************************************************************
* Blog-Kategorien
*******************************************************************************/
.blog-kategorien-thumb {
    position: relative;
    margin-bottom: 60px;
    display: block;
    @include transition(all, 0.3s, ease, 0s);

    img {
        width: 100%;
    }

    &::after {
        content: '';
        background-color: rgba($brand-color-secondary, 0.5);
        opacity: 0;
        @include absoluted-fullwidth();
        @include transition(all, 0.3s, ease, 0s);
    }

    &:focus,
    &:hover {
        &::after {
            @include transition(opacity, 0.3s, ease, 0s);
            opacity: 1;
            z-index: 1;
        }
        p {
            background-color: $brand-color;
            color: #fff;
        }
    }
}

.blog-kategorien-content {
    width: 100%;
    text-align: center;
    padding: 0 15px;
    @include horizontal-vertical-align($position: absolute);
    z-index: 2;
    
    p {
        background-color: #fff;
        padding:0 24px;
        border-bottom: 3px solid $brand-color;
        color: $body-color;
        font-weight: $bold-weight;
        letter-spacing: 4px;
        margin-bottom: 0;
        text-transform: uppercase;
        line-height: 24px;
        @extend %fontsize15;
        @include inline-block;
        @include transition(all, 0.3s, ease, 0s);
    }
}

.blog-kategorien-title-block {
    margin-bottom: 67px;

    .sub-tag {
        margin-bottom: 9px;
    }
}
@include media-breakpoint-up(md) {
    .blog-kategorien-title-block {
        margin-bottom: 83px;
    }

    .blog-kategorien-thumb {
        margin-bottom: 50px;
    }
}