/*******************************************************************************
* Content Section
*******************************************************************************/
.sub-tag {
    font-weight: $bold-weight;
    letter-spacing: 4px;
    color: $color-light-yellow;
    text-transform: uppercase;
    margin-bottom: 20px;
    @extend %fontsize15;
    @include inline-block();
}

.internal-links {
    background-color: $brand-color;
    color: #fff;
    padding: 0 5px;
    @include inline-block;

    &:hover,
    &:focus {
        background-color: $body-color;
        color: #fff;
    }
}

.title-text {
    letter-spacing: 4px;
    margin-bottom: 40px;
    text-transform: uppercase;
    color: $brand-color;
    font-weight: $bold-weight;
    @extend %fontsize21;
}

.content-section {
    .btn {
        width: auto;
    }
    h1, h2, .h1, .h2 { 
        margin-bottom: 50px;
    }
    p {
        margin-bottom: 30px;
    }
    ul {
        li {
            position: relative;
            margin-bottom: 30px;
            padding-left: 24px;

            &::before {
                content: '';
                background-image: url('../images/icons/check.svg');
                background-repeat: no-repeat;
                background-size: contain;
                position: absolute;
                left: 0;
                top: 8px;
                width: 12px;
                height: 11px;
            }
        }
    }
    ol {
        li {
            position: relative;
            margin-bottom: 30px;
            padding-left: 24px;
    
            &::before {
                content: '';
                background-image: url('../images/icons/plus.svg');
                background-repeat: no-repeat;
                background-size: contain;
                position: absolute;
                left: 0;
                top: 8px;
                width: 11px;
                height: 11px;
            }
        }
    }
}


.external-link {
    background-color: $body-color;
    color: #fff;    
    padding: 0 34px 0 6px;
    margin-bottom: 5px;
    margin-right: 5px;
    position: relative;
    @include inline-block();

    &::after {
        content: '';
        background-image: url('../images/icons/external-link.svg');
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        right: 8px;
        top: 6px;
        width: 13px;
        height: 13px;
    }

    &:hover,
    &:focus {
        color: #fff;
        background-color: $brand-color;
    }
}

.icon-link {
    font-weight: $bold-weight;
    letter-spacing: 4px;
    color: $brand-color-secondary;
    text-transform: uppercase;
    position: relative;
    padding-right: 24px;
    @extend %fontsize15;
    @include inline-block();

    &::after {
        @include transition(all, 0.3s, ease, 0s);
        content: '';
        background-image: url('../images/icons/arrow-right.svg');
        background-repeat: no-repeat;
        background-size: contain;
        position: absolute;
        right: 6px;
        top: 2px;
        width: 11px;
        height: 8px;
    }

    &:hover,
    &:focus {
        color: $brand-color;

        &::after {
            right: 0;
        }
    }
}

.bg-yellow {
    .sub-tag,
    .title-text {
        color: #fff;
    }
    &.content-section {
        h1, h2, .h1, .h2,
        .sub-tag {
            color: #fff;
        }
        .internal-links {
            background-color: #fff;
            color: $body-color;

            &:hover,
            &:focus {
                background-color: $body-color;
                color: #fff;
            }
        }
    }
    .icon-link {  
        &::after {
            background-image: url("../images/icons/arrow-right-white.svg");
        }

        &:hover,
        &:focus { 
            color: #fff;
        }
    }
    ul {
        li {
            &::before {
                background-image: url('../images/icons/check-white.svg');
            }   
        }
    }
    ol {
        li {
            &::before {
                background-image: url('../images/icons/plus-white.svg');
            }   
        }
    }
}

@include media-breakpoint-up(md) {
    .external-link {
        &::after {
            top: 7px;
            width: 15px;
            height: 15px;
        }
    }
    .content-section {
        p {
            margin-bottom: 40px;
        }
        h1, h2, .h1, .h2 { 
            margin-bottom: 75px;
        }
        ol {
            li {
                margin-bottom: 40px;
                padding-left: 32px;
        
                &::before {
                    width: 15px;
                    height: 15px;
                }
            }
        }
        ul {
            li {
                margin-bottom: 40px;
                padding-left: 32px;

                &::before {
                    width: 15px;
                    height: 14px;
                }
            }
        }
    }
    .title-text { 
        margin-bottom: 50px;
    }
    .sub-tag { 
        margin-bottom: 30px;
    }
}

@include media-breakpoint-up(lg) { 
    .icon-link {
        padding-right: 30px;

        &::after { 
            top: 7px;
            width: 15px;
            height: 12px;
        }       
    }
}

