//****************************************************************************
//  General extensions
//***************************************************************************/

%inherit_textstyles {
    color: inherit;
    font-size: inherit;
    font-weight: inherit;
    letter-spacing: inherit;
    line-height: inherit;
    text-transform: inherit;
    text-align: inherit;
    font-style: inherit;
}

//****************************************************************************
//  Font sizes extensions
//***************************************************************************/
%fontsize56 {
    font-size: 28px;
    line-height: 33px;
}
%fontsize50 {
    font-size: 24px;
    line-height: 29px;
}
%fontsize45 {
    font-size: 28px;
    line-height: 30px;
}
%fontsize40 {
    font-size: 22px;
    line-height: 28px;
}
%fontsize42 {
    font-size: 28px;
    line-height: 30px;
}
%fontsize37 {
    font-size: 37px;
    line-height: 45px;
}
%fontsize32 {
    font-size: 22px;
    line-height: 27px;
}   
%fontsize26 {
    font-size: 26px;
    line-height: 30px;
}   
%fontsize25 {
    font-size: 19px;
    line-height: 26px;
}   
%fontsize21 {
    font-size: 17px;
    line-height: 22px;
} 
%fontsize17 {
    font-size: 14px;
    line-height: 16px;
}
%fontsize15 {
    font-size: 12px;
    line-height: 14px;
}
%fontsize14 {
    font-size: 12px;
    line-height: 14px;
} 
%fontsize12 {
    font-size: 12px;
    line-height: 25px;
} 

@include media-breakpoint-up(md){
    %fontsize14 {
        font-size: 14px;
        line-height: 16px;
    }
    %fontsize32 {
        font-size: 26px;
        line-height: 310px;
    }   
    %fontsize56 {
        font-size: 36px;
        line-height: 42px;
    }
    %fontsize50 {
        font-size: 35px;
        line-height: 41px;
    }
    %fontsize45 { 
        font-size: 35px;
        line-height: 38px;
    }
    %fontsize42 {
        font-size: 32px;
        line-height: 35px;
    }
}

@include media-breakpoint-up(lg){
    %fontsize56 {
        font-size: 56px;
        line-height: 68px;
    }

    %fontsize50 {
        font-size: 50px;
        line-height: 60px;
    }

    %fontsize45 {
        font-size: 45px;
        line-height: 50px;
    }

    %fontsize42 {
        font-size: 42px;
        line-height: 50px;
    }

    %fontsize40 {
        font-size: 34px;
        line-height: 40px;
    }

    %fontsize32 {
        font-size: 32px;
        line-height: 40px;
    }

    %fontsize37 {
        font-size: 35px;
        line-height: 43px;
    }

    %fontsize26 {
        font-size: 24px;
        line-height: 30px;
    }

    %fontsize25 {
        font-size: 25px;
        line-height: 30px;
    }

    %fontsize21 {
        font-size: 21px;
        line-height: 30px;
    }
    
    %fontsize17 {
        font-size: 17px;
        line-height: 20px;
    }

    %fontsize15 {
        font-size: 15px;
        line-height: 25px;
    }
}

@include media-breakpoint-up(xl){
    %fontsize40 {
        font-size: 40px;
        line-height: 45px;
    }
}