.breadcrumb-block {
    @include flexbox();
    @include align-items(center);
    max-width: 1140px;
    padding: 20px 20px 0;
    margin: 0 auto;
    text-transform: uppercase;
    color: $brand-color-gray;

    li {
        @extend %fontsize15;        
        font-weight: $bold-weight;
        letter-spacing: 1px;
    }
    
    .breadcrumb-list {
        &:not(:last-child) {
            padding-right: 27px;
            position: relative;

            &:after {
                content: '/';
                font-weight: 700;
                position: absolute;
                right: 12px;
                top: 0;
            }
        }

        a {
            &:hover {
                color: $body-color;
            }
        }
    }
}