/**************************************************************************
* Form
***************************************************************************/
.form-group{
    margin-bottom: 22px;
    .btn{
        width: auto;
        @include inline-block;
        margin-right: 15px;
        &:last-child{
            margin-right: 0;
        }
    }
    label {
        font-weight: $bold-weight;
        letter-spacing: 4px;
        margin-bottom: 8px;
        color: $brand-color;
        @extend %fontsize15;
    }
}

.form-control {
    border: none;
    border-bottom: 1px solid $brand-color;
    border-radius: 0;
    font-size: 19px;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 0.375rem 1rem;

    &:focus {
        border-color: $brand-color;
        outline: 0;
        box-shadow: 0 0 0 0.2rem rgba(253, 185, 54, 0.4);
    }
    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: rgba($body-color, 0.4);
        opacity: 1;
    }   
    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: rgba($body-color, 0.4);
        opacity: 1;
    }
    &::-ms-input-placeholder { /* Microsoft Edge */
        color: rgba($body-color, 0.4);
        opacity: 1;
    }
    &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: rgba($body-color, 0.4);
        opacity: 1; /* Firefox */
    }
}
input[type=text]{
    width: 100%;
    margin-left: 0;
    -moz-appearance:textfield;
}
input, select, textarea {
    background-color: #fff;
    width: 100%;
    color: $brand-color;
    @include transition(all, 0.2s, ease, 0s);
    // @include placeholder(#FFB936);

    &:focus{
        outline: none;
        border-color: $brand-color;
    }
    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
        color: rgba($body-color, 0.4);
        opacity: 1;
    }   
    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: rgba($body-color, 0.4);
        opacity: 1;
    }
    &::-ms-input-placeholder { /* Microsoft Edge */
        color: rgba($body-color, 0.4);
        opacity: 1;
    }
    &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
        color: rgba($body-color, 0.4);
        opacity: 1; /* Firefox */
    }
}


select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
}

select::-ms-expand {
    display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

::-ms-clear {
  display: none;
}

input[type=text]::-ms-clear {  display: none; width : 0; height: 0; }
input[type=text]::-ms-reveal {  display: none; width : 0; height: 0; }

textarea {
    &.form-control {
        border: 1px solid $brand-color; 
        resize: none;
        height: 232px;
    }
}

input[type="radio"], input[type="checkbox"] { 
    width: auto;
    &:focus{
        outline: none;
    }
}

.form-element {
    p {
        margin-bottom: 30px;
    }
    h2 {
        margin-bottom: 25px;
    }
    .form-group { 
        margin-bottom: 32px;
    }
}

.requruire-text {
    margin-top: 8px;
    @include inline-block;
}

// Start Custom CheckBox
.form-group {
    .custom-radio,
    .custom-checkbox {
        @extend %fontsize25;
        color: $body-color;
        margin-bottom: 40px;
        margin-top: 0;
        letter-spacing: normal;
        font-weight: $regular-weight;
    }
}
.custom-checkbox {
    position: relative;
    padding-left: 40px;
    cursor: pointer;
    @include inline-block;

    input {
      position: absolute;
      opacity: 0;
      cursor: pointer;

      &:checked ~ .custom-chk:after {
        display: block;
      }
    }  
  }

.custom-chk {
    position: absolute;
    top: 3px;
    left: 0;
    height: 22px;
    width: 22px;
    border-radius: 0;
    border: 1px solid $brand-color;

    &:after {
        display: none;
        content: "";
        position: absolute;
        left: 6px;
        top: 2px;
        width: 8px;
        height: 13px;
        border: solid $brand-color;
        border-width: 0 3px 3px 0;
        @include rotate(45deg);  
    }
}
// End Custom CheckBox

// Start Custom Radio Button
.custom-radio {
    @include inline-block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 25px;
    cursor: pointer;
    @extend %fontsize25;
    
    margin-right:25px;
    input {
      position: absolute;
      opacity: 0;
      left: 0;
      cursor: pointer;
      &:checked ~ .checkmark:after {
        display: block;
      }
    }
  }
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 26.85px;
    width: 26.85px;
    background-color: #fff;
    border:1px solid $brand-color;
    border-radius: 50%;
    &:after {
        display: none;
        content: "";
        position: absolute;
        top: 7px;
        left: 7px;
        width: 11px;
        height: 11px;
        border-radius: 50%;
        background: $brand-color;
    }
}
// END Custom Radio Button

@include media-breakpoint-up(md) { 
    .custom-checkbox { 
        padding-left: 46px;
    }
    .custom-chk { 
        height: 24px;
        width: 24px;

        &::after {
            left: 7px;
            top: 2px;
            width: 9px;
            height: 15px;
        }
    }

    .form-element {
        h2 {
            margin-bottom: 47px;
        }
        .form-group { 
            margin-bottom: 44px;
        }
    }
    .form-group{ 
        label {
            margin-bottom: 11px;
        }
    }
    textarea {
        &.form-control {
            border: 1px solid $brand-color; 
            resize: none;
            height: 288px;
        }
    }
    .form-control {
        font-size: 25px;
        height: calc(2em + 0.75rem + 2px);
        padding: 0.375rem 1.5rem;
    }
}