/****************************************************************************
* Map Style
***************************************************************************/
.map-section {
    position: relative;
    background-color: $brand-color-gray-shade;
    padding: 50px 0;    
}

.map-marker {
    font-weight: $bold-weight;
    letter-spacing: 4px;
    text-transform: uppercase;
    color: $body-color;
    position: absolute;
    bottom: 0;
    left: 50%;
    z-index: 99;
    margin: 0;
    transform: translateY(0);
    opacity: 1;
    transition: transform .5s,opacity .5s;
    @extend %fontsize15;
    @include inline-block();

    &::before {
        content: '';
        display: block;
        bottom: 0;
        left: 0;
        width: 21px;
        height: 36px;        
        background: url('../images/icons/marker.svg') center center no-repeat transparent;
        background-size: contain;
        margin: 0 auto;
    }
}

.popover {
    border-radius: 0;
}

.popover-body {
    padding: 10px;
    font-weight: $bold-weight;
    color: $body-color;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.05);
    max-width: 115px;
    @extend %fontsize17;

    a {
        text-decoration: underline;
    }
}

@include media-breakpoint-up(md) {
    .popover-body {
        padding: 15px;
        max-width: initial;
    }
    .map-marker {
        &::before {
            width: 31px;
            height: 46px;
        }
    }
}