/*******************************************************************************
* Branchenhighlights
*******************************************************************************/
.accordion-block {
    background-color: transparent;
    border: 0;
    padding: 0;

    .title-text {
        margin-bottom: 15px;
    }

    .card {
        background-color: transparent;
        border: 0;
        border-radius: 0;
        margin-bottom: 40px;
    }

    .card-header {
        cursor: pointer;
        color: #fff;
        padding: 0;
        background-color: transparent;
        border-bottom: 0;
        border: 0;
        position: relative;
        height: 300px;
        display: flex;
        align-items: flex-end;

        &.bg-image {
            @include bg-img;
        }

        &::before {
            content: '';
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 75%;
            background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(16,16,16,0.6) 60%, rgba(16,16,16,0.8) 100%); /* FF3.6-15 */
            background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(16,16,16,0.6) 60%,rgba(16,16,16,0.8) 100%); /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(16,16,16,0.6) 60%,rgba(16,16,16,0.8) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6101010',GradientType=0 ); /* IE6-9 */
        }
   
        &:first-child {
            border-radius: 0;
        }

        .container {
            padding-bottom: 30px;
            position: relative;

            h2, p {
                color: inherit;
            }
            h2 {
                margin-bottom: 10px;
            }
        }

        .icon-link {
            padding-right: 32px;
            margin-bottom: 25px;
            letter-spacing: 3px;
            &:after {
                @include rotate(-90deg);
                @include transition(all, 0.3s, ease, 0s);
                top: 3px;
                right: -2px;
                width: 20px;
            }
        }

        &.collapsed {
            .icon-link {
                &:after {
                    @include rotate(90deg);
                    right: 3px;
                    top: 7px;
                }
            }
        }
    }

    .card-body {
        padding: 0;
        padding-top: 40px;
    }
}

.card-header-image {
    img {
        width: 100%;
    }
}

.accordion-title-block {
    margin-bottom: 57px;

    .sub-tag {
        margin-bottom: 9px;
    }
}

.member-thumb-slider {
    position: relative;
    > h2 {
        margin-bottom: 45px;
    }
    img {
        max-width: 140px;
    }
    .member-teaser {
        border-top: 5px solid $brand-color;
        min-height: 175px;
        margin-bottom: 0;
    }
}

.single-theme-slide {
    max-width: 100%;
}

.member-thumb-slider-thumb {
    position: relative;
    overflow: hidden;
    z-index: 1;

    &:hover,
    &:focus {
        cursor: default;
    }
}

.member-thumb-slider-counter {
    font-size: 17px;
    line-height: 20px;
    font-weight: $bold-weight;
    color: $brand-color;
    letter-spacing: 4px;
    position: absolute;
    top: 18px;
    right: 26px;
    @include inline-block();
}

@include media-breakpoint-up(sm) {
    .single-member-thumb-slide {
        max-width: 50%;
    }
 
    .member-thumb-slider-init {
        padding-top: 62px;
        .owl-nav {
            @include inline-block();
        }
    }
    .member-thumb-slider-counter {
        display: none;
    }
}
@include media-breakpoint-up(md) {
    .accordion-title-block {
        margin-bottom: 83px;
    }
    .member-thumb-slider {
        .member-teaser {
            min-height: 252px;
        }
    }
}
@include media-breakpoint-up(lg) {
    .accordion-block {
        .card {
            margin-bottom: 75px;
        }

        .card-header {
            height: 440px;

            .container {
                padding-bottom: 40px;
    
                h2 {
                    margin-bottom: 25px;
                }
            }

            .icon-link {
                padding-right: 22px;
                letter-spacing: 3.8px;
            }
        }
        .card-body {
            padding-top: 90px;
        }
    }

    .single-member-thumb-slide {
        max-width: 33%;
    }  
}
