/*******************************************************************************
* Teaser Element
*******************************************************************************/
.teaser-elemente {
    position: relative;
    overflow: hidden;
    z-index: 1;

    .row {
        [class*='col-'] {
            &:not(:last-child) {
                .teaser-elemente-thumb { 
                    margin-bottom: 76px;
                }
            }
            &:nth-child(even) {
                .teaser-elemente-tile-hover {
                    &.teaser-elemente-tile {
                        &::after {
                            top: 231px;
                        } 
                    }   
                }
                .teaser-elemente-tile {
                    &::after {
                        top: 480px;
                    }
                }
            }
        }   
    }
}

.teaser-elemente-tile-hover.teaser-elemente-tile {
    .teaser-elemente-thumb-title{
        opacity: 1;
    }

    &::after {
        top: 231px;
        opacity: 0.8;
    }
}

.teaser-elemente-thumb {
    position: relative;

    &:hover {
        cursor: default;
    }
}


.teaser-elemente-tile {
    margin-bottom: 26px;
    position: relative;
    z-index: -1;

    &::after {
        content: '';
        background-color: $brand-color;
        position: absolute;
        left: 69px;
        top: -480px;
        width: 209px;
        height: 219px;
        border-top-left-radius: 57px;
        opacity: 0;
        @include rotate (253deg);
        @include transition(all, 0.5s, ease, 0s);
    }
}

.teaser-elemente-image {
    height: 375px;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }
}

.teaser-elemente-content {
    padding: 0 30px;
    z-index: 2;

    .icon-link {
        margin-top: 8px;
    }
}

.teaser-elemente-title {
    h2 {
        margin-bottom: 50px;
    }
}

.teaser-elemente-thumb-title {
    opacity: 0;
    position: absolute;
    left: 30px;
    bottom: 25px;
    z-index: 1;
    @include transition(opacity, 0.5s, ease,  0s);
    
    h2 {
        font-weight: $regular-weight;
        @extend %fontsize37;
        color: #fff;
        margin-bottom: 0;
    }
}

.bg-yellow {
    .teaser-elemente-thumb-title {
        h2 {
            color: $brand-color;
        }
    }
    .teaser-elemente-tile {
        &::after {
            background-color: #fff;
        }
    }
}

@include media-breakpoint-up(md) {
    .teaser-elemente {
        .no-gutters {
            > .col, 
            > [class*="col-"]{
                padding-left: 25px;
                padding-right: 25px;
            }
        }

        .row {
            [class*='col-'] {
                &:nth-child(even) {
                    .teaser-elemente-tile-hover {
                        .teaser-elemente-tile {
                            &::after {
                                top: 190px;
                            } 
                        }   
                    }
                }
            }   
        }
    }
    .teaser-elemente-tile {
        &::after {
            width: 280px;
            height: 281px;
        }
        &.teaser-elemente-tile-hover {
            &::after { 
                top: 190px;
            }
        }
    }
    .teaser-elemente-three-column {
        .teaser-elemente-image {
            height: 280px;
        }
        .teaser-elemente-content {
            padding: 0;

            .icon-link {
                margin-top: 23px;
            }
        }
        .teaser-elemente-thumb-title {
            left: 28px;
            bottom: 26px;

            h2 {
                font-size: 35px;
                line-height: 43px;
            }
        }
        .row {
            [class*='col-'] {
                &:not(:last-child) {
                    .teaser-elemente-thumb {
                        margin-bottom: 75px;
                    }
                }
                &:nth-child(even) {
                    .teaser-elemente-tile-hover {
                        &.teaser-elemente-tile {
                            &::after {
                                top: 126px;
                            } 
                        }   
                    }
                }
            }   
        }
        .teaser-elemente-tile-hover {
            &.teaser-elemente-tile {
                &::after {
                    top: 126px;
                } 
            }   
        }
        .teaser-elemente-tile {
            margin-bottom: 45px;

            &::after {
                width: 222px;
                height: 223px;
                left: 38px;
                border-top-left-radius: 60px;
            } 
        }    
    }
    .teaser-elemente-thumb-title { 
        left: 44px;
        bottom: 66px;
    }
    .teaser-elemente-content {
        padding: 0 44px;

        .icon-link {
            margin-top: 20px;
        }
    }
}

@include media-breakpoint-up(lg) {
    .teaser-elemente-title {
        h2 {
            margin-bottom: 75px;
        }
    }
    .teaser-elemente { 
        .row {
            [class*='col-'] {
                &:not(:last-child) {
                    .teaser-elemente-thumb {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    .teaser-elemente-three-column {
        .row {
            [class*='col-'] {
                &:not(:last-child) {
                    .teaser-elemente-thumb {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    .teaser-elemente-thumb-title {
        h2 {
            font-size: 56px;
            line-height: 68px;
        }  
    }
    .teaser-elemente-image {
        height: 440px;
    }
    .teaser-elemente-tile {
        &::after {
            width: 351px;
            height: 350px;
            border-top-left-radius: 101px;
        }
    }
}