/*******************************************************************************
* Button
*******************************************************************************/
.btn{
    padding: 8px 25px;
    margin: 0;
    outline: 0;
    box-shadow: none;
    white-space: normal;
    text-transform: uppercase;
    border-radius: 0;
    border-bottom-right-radius: 25px;
    letter-spacing: 2.5px;
    border: 1px solid $brand-color;
    color: $white;
    font-weight: $bold-weight;
    font-family: $font-primary;
    background-color: $brand-color;
    @include transition(all,0.2s,ease,0s);
    @include inline-block();
    @extend %fontsize17;
    
    &:hover,&:focus, &:active{
        background-color: #fff;
        color: $body-color;
        box-shadow: none;
    }
}
button:focus {
    outline: none;
}
.bg-yellow {
    .btn {
        border-color: #fff;
        background-color: #fff;
        color: $body-color;

        &:hover,&:focus, &:active{
            background-color: $brand-color;
            color: $body-color;
        }
     }
}
@include media-breakpoint-up(sm){
    .btn{
        padding: 9px 49px;
    } 
}
