/*******************************************************************************
* Unternehmensvorstellung
*******************************************************************************/
.member-section {
    .col-sm-6 {
        padding-left: 0;
        padding-right: 0;
    }
}
.member-teaser {
    position: relative;
    padding: 20px;
    margin-bottom: 20px;
    min-height: 175px;
    display: block;
    background-color: rgba($brand-color-gray, 0.1);
    text-align: center;
    @include transition(all, 0.3s, ease, 0s);

    .member-image {
        position: absolute;
        @include horizontal-align();
        top: 32px;
        opacity: 0;
    }

    .member-image-default {
        opacity: 1;
    }

    .member-image-hover {
        opacity: 0;
    }

    h3, h4 {
        text-transform: uppercase;
        display: none;
        @include transition(all, 0.3s, ease, 0s);
    }

    h3 {
        @extend %fontsize21;
        line-height: 28px;
        margin-bottom: 5px;
        letter-spacing: 5.2px;
    }

    h4 {
        @extend %fontsize15;
        line-height: 25px;
        letter-spacing: 4px;
    }

    &:focus,
    &:hover {
        background-color: $brand-color;
        
        h3, h4 {
            color: #fff;
        }

        .icon-link {
            &:after {
                background-image: url("../images/icons/arrow-right-white.svg");
            }
        }
        
        .member-image-hover {
            opacity: 1;
        }
        .member-image-default {
            opacity: 0;
        }
    }
}

.member-teaser-link {
    width: 100%;
    text-align: center;
    padding: 14px;
    position: absolute;
    @include horizontal-align();
    bottom: 0;
    z-index: 2;

    .icon-link {
        color: inherit;
    }
}

.member-title-block {
    margin-bottom: 67px;

    .sub-tag {
        margin-bottom: 9px;
    }
}

.modal-open {
    .site-main {
        z-index: 9999;
    }
}

.member-modal {
    padding-right: 0 !important;

    .modal-dialog {
        padding: 0;
        margin: 0;
        width: 100%;
        max-width: 100%;
    }
    
    .modal-content {
        height: 100%;
        border-radius: 0;
        border: 0;
        padding-top: 90px;
    }

    .close:not(:disabled):not(.disabled):hover, .close:not(:disabled):not(.disabled):focus {
        opacity: 1;
    }

    button.close {
        cursor: pointer;
        padding: 0;
        height: 22px;
        width: 22px;
        opacity: 1;
        float: none;
        text-align: center;
        position: absolute;
        right: 10px;
        top: 27px;
        z-index: 3;  
        @include transition(all, 0.2s, ease, 0s);

        .bars {
            background-color: $body-color;
            border-radius: 0;
            height: 2px;
            border-radius: 25px;
            margin-bottom: 4px;
            width: 100%;
            display: block;

            &:first-child {
                -webkit-transform: translateY(5px) rotate(136deg);
                transform: translateY(5px) rotate(136deg);
            }
            &:last-child {
                -webkit-transform: translateY(-7px) rotate(-136deg);
                transform: translateY(-7px) rotate(-136deg);
                margin-bottom: 0;
            }
            &:nth-child(2) {
                -webkit-transform: scale(0);
                transform: scale(0);
            }
        }
        &::after {
            content: '';
            background-color: $brand-color;
            position: absolute;
            right: 7px;
            top: -5px;
            width: 30px;
            height: 30px;
            border-bottom-right-radius: 8px;
            z-index: -1;
            transform: rotate(72deg);
        }
    }
}

.member-slider {
    .container {
        padding-left: 0;
        padding-right: 0;
    }
}

.member-slide {
    padding-bottom: 84px;
}

.member-slider-block {
    position: relative;

    .member-slider-init {
        position: relative;
    }

    .slider-counter {
        position: absolute;
        top: 14px;
        right: 18px;
    }
}

.member-slider-init {
    overflow-x: hidden;
    
    &.owl-carousel {
        .owl-nav {
            display: none;
        }
    }
    &.owl-loaded {
        overflow-x: visible;
    }
}

.member-slider-title {
    position: absolute;
    left: 0;
    bottom: 74px;
    z-index: 1;
    padding: 0 30px;
    @include transition(opacity, 0.5s, ease,  0s);
    
    h2 {
        font-size: 37px;
        line-height: 45px;
        color: #fff;
    }
}

.member-slide-image {
    height: 375px;
    position: relative;
    z-index: 1;

    img {
        height: 100%;
        width: 100%;
        object-fit: cover;
    }

    &::before {
        content: '';
        width: 100%;
        height: 5px;
        background-color: $brand-color;
        position: absolute;
        top: 0;
        right: 0;
        @include transition(all, 0.5s, ease, 0s);
    }
}

.member-slide-content {
    padding: 0 30px 84px 30px;
    
    .title-text { 
        margin-bottom: 30px;
    }
    
    .external-link {
        line-height: 24px;

        &::after {
            top: 6px;
        }
    }
    
    &.content-section ul {
        margin-bottom: 40px;
        
        li {
            margin-bottom: 15px;
            @include inline-block;
        }
    }
}

.member-slide-content--inner {
    max-width: 862px;
}

.member-slide-logo {
    display: flex;
    margin-bottom: 1.4rem;
    
    img {
        max-width: 184px;
        max-height: 100px;
    }
}


.owl-item:not(:first-child) {
    .member-slide-hover.member-slide {
        .member-slider-title {
            opacity: 1;
        }
        .member-slide-image::before {
            right: -100%;
        }
        .member-slide-image::after {
            left: -100px;
            opacity: 0;
        }
    }    
}

.owl-item:nth-child(n) .member-slide-hover.member-slide-update,
.owl-item.active .member-slide-hover.member-slide-update,
.firstActiveItem  .member-slide-update.member-slide-hover {
    .member-slider-title {
        opacity: 1;
    }
    .member-slide-image::before {
        right: 0;
    }
}

.member-slide-hover {
    .member-slider-title {
        opacity: 1;
    }
    .member-slide-image::before {
        right: 0;
    }
    .member-slide-image::after {
        left: 7.2%;
        opacity: 0.8;
    }
}

@include media-breakpoint-up(sm) {
    .member-section {
        .col-sm-6 {
            padding-left: 30px;
            padding-right: 30px;
        }
    }
    .member-teaser {
        padding: 130px 20px 84px;
        margin-bottom: 60px;
        min-height: 356px;

        .member-image {
            top: 43px;
        }
        h3, h4 {
            display: block;
        }
    }
    .member-teaser-link {
        padding: 20px;
    }
}
@include media-breakpoint-up(md) {
    .member-slide-content {
        .external-link {
            &:after {
                top: 4px;
            }
        }
    }
    .member-slide-logo { 
        justify-content: flex-end;
    }
    
    .member-title-block {
        margin-bottom: 83px;
    }

    .member-teaser {
        margin-bottom: 50px;
    }
}
@include media-breakpoint-up(lg) {
    .member-slider-block {
        .slider-counter {
            top: 70px;
        }
    }
    .member-teaser {
        min-height: 376px;
    }
    .member-modal {
        .modal-content {
            padding-top: 133px;
        }

        button.close {
            height: 26px;
            width: 26px;
            right: 10px;
            top: 20px;
            &::after {
                width: 36px;
                height: 36px;
            }
        }
    }

    .member-slider-init {
        padding-top: 62px;

        &.owl-carousel {
            .owl-nav {
                display: block;
            }
        }
    }

    .member-slider {
        .container {
            padding-left: 25px;
            padding-right: 25px;
        }
    }

    .member-slide {
        padding-bottom: 64px;
    }

    .member-slide-image {
        height: 440px;

        &::before {
            right: -100%;
        }
    }

    .member-slider-title {
        opacity: 0;
        bottom: 126px;
        padding: 0 60px;

        h2 {
            font-size: 56px;
            line-height: 68px;
        }
    }

    .member-slide-logo {
        img {
            max-width: 218px;
            max-height: 131px;
        }
    }

    .member-slide-content {
        padding-bottom: 64px;

        .title-text { 
            margin-bottom: 35px;
        }

        &.content-section ul {
            li {
                margin-bottom: 25px;
            }
        }
    }
}

@include media-breakpoint-up(xl) {
    .member-modal {
        button.close {
            right: 30px;
            top: 33px;
        }
    }
}
