/*******************************************************************************
* Testimonial Video 
*******************************************************************************/
.testimonial-video-section {
    background-color: $brand-color;
}

.testimonial-video {
    padding: 40px 0;
    margin: 0 auto;
    max-width: 960px;
    .sub-tag {
        color: #fff;
        margin-bottom: 8px;
    }
}

.testimonial-cover-image {
    @include bg-img;
    margin: 20px -30px;
    color: inherit;
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.45);
        z-index: 1;
        opacity: 1;
        @include transition(all,0.2s,ease,0s);
    }

    &.video-played {
        .testimonial-video-profile {
            opacity: 0;
            pointer-events: none;
        }
    }
}

.testimonial-video-profile {
    padding: 15px 15px 16px 29px;
    color: #fff;
    max-width: 360px;
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 2;
    opacity: 1;
    @include transition(all,0.2s,ease,0s);

    &:before {
        content: '';
        border: 0;
        background-color: rgba($brand-color, 0.8);
        border-radius: 0;
        border-top-left-radius: 24px;
        width: 78px;
        height: 78px;
        position: absolute;
        left: -16px;
        top: -9px;
        z-index: -1;
        @include rotate(30deg);
    }
    
    p {
        margin-bottom: 0;
            &.profile-name {
            font-weight: $bold-weight;
        }
    }
}

.testimonial-video-text {
    padding: 8px 0;
}

@include media-breakpoint-up(md) {
    .testimonial-cover-image {
        margin: 20px 0;
    }
}

@include media-breakpoint-up(lg) {
    .testimonial-video-profile {
        padding: 15px 15px 34px 98px;
        left: 50px;
        max-width: 550px;
        &:before {
            border-top-left-radius: 40px;
            width: 128px;
            height: 128px;
            left: 25px;
            top: 32px;
        }

    }
    .testimonial-video-text {
        padding: 26px 0;
    }
}
@include media-breakpoint-up(xl) {
    .testimonial-video {
        padding: 60px 0;
    }
}

@include media-breakpoint-up(xxl) {
    .testimonial-video {
        padding: 80px 0;
    }
}
@include media-breakpoint-down(sm) {
    .testimonial-video-profile {       
        p {
            font-size: 12px;
            line-height: 14px;
            &.profile-name {
                font-size: 14px;
                line-height: 17px;
            }
        }
    }
}
