/*******************************************************************************
* Video 
*******************************************************************************/
.video {
    margin: 0 auto;
}

.cover-image {
    @include bg-img;
    margin: 0 -30px;
    color: inherit;
    position: relative;

    &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.45);
        z-index: 1;
        opacity: 1;
        @include transition(all,0.2s,ease,0s);
    }
}

.video-played {
    &:before,
    .play-button {
        opacity: 0;
        pointer-events: none;
    }

    .video-area {
        iframe, 
        video {
          opacity: 1;
        }
    }
}

.play-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba($brand-color, 0.83);
    width: 83px;
    height: 83px;
    border-radius: 50%;
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    opacity: 1;
    @include translate(-50%, -50%);
    @include transition(all,0.2s,ease,0s);
    z-index: 2;

    img {
        padding-left: 10px;
        max-height: 43px;
    }
}

.video-area {
    position: relative;
    overflow: hidden;
    padding-bottom: 56.2%;
    iframe,
    video {
        border: 0;
        width: 100%;
        opacity: 0;
        @include absoluted-fullwidth();
        @include transition(all,0.2s,ease,0s);
    }
}

@include media-breakpoint-up(md) {
    .cover-image {
        margin: 0;
    }
}

@include media-breakpoint-up(lg) {
    .video-area {
        iframe,
        video {
            @include horizontal-vertical-align($position: absolute);
            height: 100%;
        }
    }

    .play-button {
        width: 109px;
        height: 109px;
    
        img {
            padding-left: 15px;
            max-height: 60px;
        }
    }
}
