/*******************************************************************************
* Blog-Teaser
*******************************************************************************/
.row {
    [class*='col-'] {
        &:not(:last-child) {
            .blog-teaser-thumb {
                margin-bottom: 100px;
            }
        }
    }
}

.blog-title {
    @extend %fontsize21;
    letter-spacing: 2.5px;
    margin-bottom: 30px;

    h5 {
        color: $brand-color;
    }
}

.blog-teaser-thumb {
    &:hover {
        cursor: context-menu;
    }
}

.blog-all-teaser-link {
    margin-top: 75px;
}

.blog-teaser-title-block {
    margin-bottom: 75px;
    position: relative;

    .sub-tag {
        margin-bottom: 9px;
    }
}

.blog-teaser-image {
    margin-bottom: 30px;
}

.blog-teaser-content {
    margin-bottom: 40px;

    .sub-tag {
        margin-bottom: 9px;
    }

    h5 {
        display: block;
    }

    p {
        color: $body-color;
        margin-bottom: 0;
    }
}

// Pagination Style (Blog-list Style)
.blog-teaser-list {
    .row [class*='col-'] {
        &:not(:last-child) {
            .blog-teaser-thumb {
                margin-bottom: 75px;        
            }
        }
    }
    .blog-teaser-thumb {
        margin-bottom: 75px;
    }
}
.blogpagination__item {
    border: 1px solid $body-color;
    @include transition(all, 0.5s, ease, 0s);
}

.blogpagination__link {
    border: 0;
    padding: 2px 10px;
    margin: 0;
    @extend %fontsize21;
    
    &:hover {
        background-color: $brand-color;
        color: #fff;
    }
}

.blogpagination__item--active {
    .blogpagination__item--current {
        background-color: $brand-color;
        .blogpagination__itemtitle {
            color: #fff;
        }
    }
}

.blog-detail-image {
    margin-bottom: 30px;
}

.blog-detail-image .blog-description {
    margin-bottom: 20px;
}

.news-backlink {
    @extend %fontsize15;
    font-weight: $bold-weight;
    padding-left: 34px;
    position: relative;
    letter-spacing: 2.5px;

    &:before {
        @include transition(all, 0.3s, ease-in-out, 0s);
        content: "";
        background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='19.548' height='13.949' viewBox='0 0 19.548 13.949'><path d='M15.366,11.871a1,1,0,0,1,1.421,0,1.02,1.02,0,0,1,0,1.421L10.831,19.25a1.03,1.03,0,0,1-1.45,0L3.424,13.293a1.005,1.005,0,1,1,1.421-1.421L9.1,16.137V1a1,1,0,0,1,1.009-1,.989.989,0,0,1,1,1V16.136Z' transform='translate(19.548 -3.126) rotate(90)' fill='rgb(255, 185, 54)'/></svg>");
        background-repeat: no-repeat;
        background-size: contain;
        width: 14px;
        height: 19px;
        position: absolute;
        left: 0;
        top: 7px;        
    }

    &:hover {
        color: $brand-color;

        &::before {
            left: -6px;
        }
    }
}

.blog-description {
    margin-bottom: 25px;
}

@include media-breakpoint-up(sm) {
    .blog-teaser-thumb {
        margin-bottom: 75px;
    }

    .row {
        [class*='col-'] {
            &:not(:last-child) {
                .blog-teaser-thumb {
                    margin-bottom: 75px;
                }
            }
        }
    }

    .blog-all-teaser-link {
        margin-top: 0px;
        text-align: center;
    }
}

@include media-breakpoint-up(md) {
    .blog-title {
        margin-bottom: 40px;
    }

    .postlist--recent {
        .row {
            [class*='col-'] {
                &:not(:last-child) {
                    .blog-teaser-thumb {
                        margin-bottom: 0;
                    }
                }
            }
        }
    }
    
    .blog-all-teaser-link {
        margin-top: 100px;
    }

    .blog-teaser-thumb {
        margin-bottom: 0;
    }

    .blog-teaser-title-block {
        margin-bottom: 100px;
    }
    
    .blog-teaser-image {
        margin-bottom: 50px;
    }

    .blog-teaser-content {
        margin-bottom: 65px;
    }

    .blog-detail-image {
        margin-bottom: 48px;
    }
    
    .blog-detail-image .blog-description {
        margin-bottom: 30px;        
    }

    .blog-description {
        margin-bottom: 40px;
    }
}
