/*******************************************************************************
* All Slider (Hero-visual-mit-content)
*******************************************************************************/
.hero-visual-mit-content {
    position: relative; 
    margin-bottom:40px;
    @include transition(all, 0.3s, ease, 0s);
}

.slider-overlay {
    background: transparent;
    height: 100%;
    width: 100%;
    position: absolute;
    left: 0;
    opacity: 9.6;
    top: 0;
    z-index: 2;
    @include transition(all, 0.4s, ease, 0s);

    &.slider-overlay--active {
        background: linear-gradient(to bottom, rgba($brand-color-secondary, 0), rgba($brand-color-secondary, 0.7));
    }
}


.hero-visual-mit-content .landing-slide,
.hero-visual-mit-content .landing-slide-tablet,
.landing-slide-mobile { 
    height: 100%;
}

.hero-visual-mit-content .landing-slide-tablet,
.hero-visual-mit-content .landing-slide {
    display: none;
}
  
.slider-content-block {
    padding: 0 30px 0 15px;
    position: absolute;
    display: block;
    z-index: 99;
    bottom: 64px;

    .slider-shape {
        background-color: $brand-color;
        position: absolute;
        left: -302px;
        top:146px;
        width: 320px;
        height: 302px;
        border-top-left-radius: 71px;
        opacity: 0.8;
        z-index: -1;
        @include rotate (160deg);
        @include transition(all, 0.5s, ease, 0s);
    }
}

.slider-title {
    transform: translateY(150%);
    -webkit-transform: translateY(150%);
    -moz-transform: translateY(150%);
    -ms-transform: translateY(150%);
    -o-transform: translateY(150%);
    position: relative;
    @include transition(transform, 0.5s, ease, 0s);

    img {
        position: absolute;
        bottom: -40px;
        left: 0;
        width: 14px;
        height: 20px;
    }

    > span {
      color: #fff;
      font-weight: $bold-weight;
      letter-spacing: 4px;
      text-transform: uppercase;
      @include inline-block();
      @extend %fontsize15;
    }
}

.slider-content {
    h1{
        color: #fff;
        font-weight: $regular-weight;
        @extend %fontsize56;
        span {
          color: $brand-color;
        }
    }
}

.slider-content-text {
    opacity: 0;
    margin-top: 45px;
    @include transition(opacity, 0.2s, ease, 0s);
    p {
        color: #fff;
        margin-bottom: 0;
    }
}

@include media-breakpoint-up(md) {
    .hero-visual-mit-content .landing-slide-tablet {
        display: block;
    }
    .landing-slide-mobile {
        display: none;
    }
    .slider-title {
        @include transition(transform, 0.5s, ease, 0s);
        transform: translateY(85%);
        -webkit-transform: translateY(85%);
        -moz-transform: translateY(85%);
        -ms-transform: translateY(85%);
        -o-transform: translateY(85%);
    }
    .slider-content-block {
        padding: 0 15px 0 ;
        bottom: 102px;

        &:after {
            display: none;
        }
        .slider-shape {
            left: -274px;
            top: 47px;
            width: 343px;
            height: 343px;
            border-top-left-radius: 90px;
            @include rotate (156deg);
            @include transition(all, 0.5s, ease, 0s);
        }
    }
    .slider-content-text {
        margin-top: 38px;
    }
}

@include media-breakpoint-up(lg) { 
    .hero-visual-mit-content .landing-slide {
        display: block;
    }
    .hero-visual-mit-content .landing-slide-tablet {
        display: none;
    }
    .slider-content {
        max-width: 860px;
        h1 {
            @include transition(transform, 0.5s, ease, 0s);
        }
    }
}

@media (min-width: 1500px) {
    .slider-content-block {
        .slider-shape {
            left: -264px;
            top: -42px;
            width: 531px;
            height: 531px;
        }
    }
}