/*******************************************************************************
* Search
*******************************************************************************/
.search-title-block {
    padding: 27px 0 14px;

    .sub-tag {
        color: $brand-color-gray;
        margin-bottom: 18px;
    }
}

.tx-indexedsearch-searchbox {
    width: 100%;
    position: relative;
    margin-bottom: 44px;

    .form-control {
        padding: 12px 45px 12px 15px;
    }

    .search-btn {
        background-color: $white;
        border: 0;
        border-radius: 0;
        position: absolute;
        right: 0;
        top: 0;
        padding: 7px 14px;
        
        svg {
            path {
                @include transition(all, 0.5s, ease, 0s);
                fill: $brand-color;
            }
        }
        &:hover,
        &:focus {
            background-color: transparent;
            svg {
                path {
                    fill: $body-color;
                }
            }
        }
    }
}

.tx-indexedsearch-result-number {
    margin-bottom: 40px;
    display: block;
    width: 100%;
}

.tx-indexedsearch-info-sword,
.tx-indexedsearch-browsebox,
.tx-indexedsearch-browsebox p {
    color: $brand-color;
    display: inline;
    font-weight: $bold-weight;
    text-transform: uppercase;
    letter-spacing: 4.3px;
    font-size: 17px;
    line-height: 23px;
}

.tx-indexedsearch-res {
    @include inline-block;
    margin-bottom: 33px;
    width: 100%;
}

.tx-indexedsearch-title {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 3px;
    font-weight: $bold-weight;
    margin-bottom: 7px;
    text-transform: uppercase;
    @include inline-block;
}
.tx-indexedsearch-redMarkup {
    text-decoration: underline;
    font-weight: normal;
}

p.tx-indexedsearch-description {
    margin-bottom: 4px;
}

ul.tx-indexedsearch-browsebox {
    display: flex;
    margin: 26px 0 65px;
    li {
        margin-bottom: 10px;
        padding-left: 20px;
        line-height: 1;

        &::before {
            display: none;
        }
        &:not(.nav-prev):not(.nav-next) {
            a {
                border: 1px solid $brand-color;
                @include inline-block;
                width: 24px;
                height: 24px;
                background-color: $brand-color;
                color: #fff;
                font-size: 21px;                
                line-height: 24px;
                font-weight: $regular-weight;
                text-align: center;
                letter-spacing: 0;
                line-height: 1;
            }
        }
        &:first-child {
            padding-left: 0;
        }
        &.tx-indexedsearch-browselist-currentPage {
            a {
                color: $brand-color !important;
                background-color: transparent !important;
            }
        }
        a {
            line-height: 21px;
            @include inline-block;
        }
    }
}

@include media-breakpoint-up(md) { 
    .search-title-block {
        padding: 37px 0;
    }

    .tx-indexedsearch-searchbox {
        margin-bottom: 90px;
    
        .form-control {
            padding: 12px 30px 12px 0;
        }
    
        .search-btn {
            padding: 18px 25px;
        }
    }

    .tx-indexedsearch-result-number {
        margin-bottom: 54px;
    }

    .tx-indexedsearch-info-sword,
    .tx-indexedsearch-browsebox,
    .tx-indexedsearch-browsebox p {
        letter-spacing: 5.25px;
        font-size: 21px;
        line-height: 35px;
    }

    .tx-indexedsearch-res {
        margin-bottom: 53px;
    }

    .tx-indexedsearch-title {
        font-size: 15px;
        line-height: 25px;
        letter-spacing: 4px;
    }

    ul.tx-indexedsearch-browsebox {
        margin: 20px 0 80px;
        li {
            padding-left: 25px;
        }
    }
}
