//****************************************************************************
//  Bootsrap variables overrides
//***************************************************************************/
$font-family-sans-serif:      'Jaldi', Arial, Helvetica, sans-serif;
$font-secondary:              'Viga', Arial, Helvetica, sans-serif;
$font-primary:                $font-family-sans-serif;
$body-color:                  #1A1A1A;
$btn-box-shadow:              none;

// Grid breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1280px,
  xxl: 1920px,
) !default;

//****************************************************************************
//  Custom variables 
//***************************************************************************/
$brand-color:                 #FFB936;
$color-light-yellow:          #F0BD42;
$brand-color-secondary:       #101010;
$brand-color-gray:            #A2A2A2;
$brand-color-light-gray:      #C7C7C7;
$brand-color-gray-shade:      #ECECEC;
$input-color:                 $body-color;

//****************************************************************************
//  Font Weight
//***************************************************************************/
$light-weight:                 300;
$regular-weight:               400;
$semi-bold-weight:             600;
$bold-weight:                  700;