/*******************************************************************************
* Horizontale Line
*******************************************************************************/
.horizontale-line-style {
    height: 1px;
    background-color: $brand-color-light-gray;
    width: 100%;
}
.horizontale-line--bg-yellow {
    background-color: $brand-color;
    
    .horizontale-line-style {
        background-color: #fff;
    }
}
.horizontale-line--fluid {
    .container-fluid {
        padding-left: 0;
        padding-right: 0;
    }
}
