/*******************************************************************************
* Login
*******************************************************************************/
.full-screen {
    &.site-main {
        margin-top: 0;
    }
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    height: 100vh;
    min-height: 530px;
    position: relative;

    .row {
        margin-left: 0;
        margin-right: 0;
    }

    &::before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 50%;
        background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(16,16,16,0.6) 60%, rgba(16,16,16,0.8) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(16,16,16,0.6) 60%,rgba(16,16,16,0.8) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(16,16,16,0.6) 60%,rgba(16,16,16,0.8) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#a6101010',GradientType=0 ); /* IE6-9 */
    }
}
.login-form-wrap {
    width: 100%;
    max-width: 317px;
    position: relative;
    z-index: 1;

    .form-group:not(:last-child) {
        margin-bottom: 32px;
    }

    p { 
        color: #fff;
        font-size: 16px;
        line-height: 20px;
    }
}
.login-form {
    background-color: #fff;
    padding: 20px 20px 8px;
    margin-bottom: 20px;

    h2 {
        font-family: $font-primary;
        font-weight: $bold-weight;
        font-size: 19px;
        line-height: 23px;
        color: inherit;
        margin-bottom: 47px;
        text-transform: uppercase;
        letter-spacing: 0.34rem;
    }
    a { 
        font-size: 14px;
        line-height: 25px;
        font-weight: $bold-weight;
        text-transform: uppercase;
        letter-spacing: 3.3px;
    }

    .forgot-password {
        display: block;
        margin-top: -26px;
        margin-bottom: 52px;
    }
}

@include media-breakpoint-up(sm) {
    .login-form-wrap {
        max-width: 480px;
    }
}

@include media-breakpoint-up(md) {
    .login-form-wrap {
        max-width: 523px;

        .form-group:not(:last-child) {
            margin-bottom: 40px;
        }
        p { 
            text-align: center;
            font-size: 19px;
            line-height: 26px;
        }
    }
    .login-form {
        padding: 40px 40px 22px;
        h2 {
            font-size: 24px;
            line-height: 28px;
            letter-spacing: 0.4rem;
            margin-bottom: 44px;
        }
        .forgot-password {
            margin-top: -40px;
            margin-bottom: 35px;
        }
    }
}

@include media-breakpoint-down(sm) {
    .login-form-wrap p br {
        display: none;
    }
}