/****************************************************************************
 * Fonts
 ***************************************************************************/

// Viga Regular
@font-face {
    font-family: 'Viga';
    src: url('../font/VigaRegular.eot');
    src: url('../font/VigaRegular.eot?#iefix') format('embedded-opentype'),
        url('../font/VigaRegular.woff2') format('woff2'),
        url('../font/VigaRegular.woff') format('woff'),
        url('../font/VigaRegular.ttf') format('truetype'),
        url('../font/VigaRegular.svg#VigaRegular') format('svg');
    font-weight: 400;
    font-style: normal;

}

// Jaldi Regular
@font-face {
    font-family:'Jaldi';
    src: url('../font/JaldiRegular.eot');
    src: url('../font/JaldiRegular.eot?#iefix') format('embedded-opentype'),
        url('../font/JaldiRegular.woff2') format('woff2'),
        url('../font/JaldiRegular.woff') format('woff'),
        url('../font/JaldiRegular.svg#JaldiRegular') format('svg');
    font-weight: 400;
    font-style: normal;
}

// Jaldi Bold
@font-face {
    font-family:'Jaldi';
    src: url('../font/JaldiBold.eot');
    src: url('../font/JaldiBold.eot?#iefix') format('embedded-opentype'),
        url('../font/JaldiBold.woff2') format('woff2'),
        url('../font/JaldiBold.woff') format('woff'),
        url('../font/JaldiBold.ttf') format('truetype'),
        url('../font/JaldiBold.svg#JaldiBold') format('svg');
    font-weight: 700;
    font-style: italic;
}

/****************************************************************************
  Body
***************************************************************************/
body {
    @extend %fontsize25;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-hyphens: manual;
    -moz-hyphens: manual;
    -ms-hyphens: manual;
    hyphens: manual;
    word-break: break-word;
}
.bg-yellow {
    background-color: $brand-color;
}
p {
    font-weight: $regular-weight;
    margin-bottom: 15px;
}
img{
    max-width: 100%;
    height: auto;
}
figure{
    margin: 0 0 15px;
}
ul, ol {
    list-style-type: none;
    padding: 0;
}
/****************************************************************************
  Links
 ***************************************************************************/
a {
    color: inherit;
    outline: none;
    @include transition(all,0.2s,ease,0s);
    font-family: $font-primary;
    &:hover, &:focus {
        color: inherit;
        text-decoration: none;
    }
    span{
        @include transition(all,0.2s,ease,0s);
    }
}

strong, b {
    font-weight: $bold-weight;
}

::-moz-selection { /* Code for Firefox */
    color: $white;
    background: $brand-color;
}

::selection {
    color: $white;
    background: $brand-color;
}

/****************************************************************************
  Headings
 ***************************************************************************/
h1, h2, .h1, .h2 {
    font-family: $font-secondary;
    color: $brand-color;
    margin-bottom: 0;
    @extend %fontsize50;
}

.h1, .h1 {
    font-weight: $bold-weight;
}

.h2, .h2 {
    font-weight: $regular-weight; 
}

h3, h4, h5, h6, .h3, .h4, .h5, .h6 {
    font-family: $font-primary;
    font-weight: $bold-weight;
    color: $brand-color;
    margin-bottom: 0;
}

h3{
    @extend %fontsize42;
}
h4{
    @extend %fontsize37;
}
h5{
    @extend %fontsize25;
    color: $body-color;
}

/****************************************************************************
  Guttter Spacing
 ***************************************************************************/
 .container{
    max-width: 1190px;
}

[class*='col-'],
.container,
.container-fluid {
    padding-left: 30px;
    padding-right: 30px;
}

.row {
    margin-left: -30px;
    margin-right: -30px;
}

@include media-breakpoint-up(md) {
    [class*='col-'] {
        padding-left: 25px;
        padding-right: 25px;
    }

    .container,
    .container-fluid {
        padding-left: 45px;
        padding-right: 45px;
    }   

    .row {
        margin-left: -25px;
        margin-right: -25px;
    }
}
