/*******************************************************************************
* All Slider (Hero-visual-mit-buttons)
*******************************************************************************/
.hero-visual-mit-buttons {
    position: relative; 
    margin-bottom:40px;

    &::after {
        content: '';
        background-image: linear-gradient(to bottom, rgba($brand-color-secondary,0), $brand-color-secondary);
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        opacity: 9.6;
        top: 0;
        z-index: 2;
    }
}
  
.slider-contents {
    padding: 0 30px 0 15px;
    position: absolute;
    display: block;
    z-index: 99;
    bottom: 64px;
}

.landing-slider-content {
    h1{
        transform: translateY(100%);
        font-weight: $regular-weight;
        @include transition(transform, 0.2s, ease, 0s);
        @extend %fontsize56;
        color: #fff;
        span {
            color: $brand-color;
        }
    }
}

.hero-visual-mit-buttons .landing-slide,
.hero-visual-mit-buttons .landing-slide-tablet,
.landing-slide-mobile { 
    height: 100%;
}

.hero-visual-mit-buttons .landing-slide-tablet,
.hero-visual-mit-buttons .landing-slide {
    display: none;
}

.slider-action-link {
    opacity: 0;
    margin-top: 44px;
    @include transition(opacity, 0.2s, ease, 0s);
    li {
        margin-bottom: 0;
        &:first-child {
            a {
                margin-bottom: 23px;
            }
        }

        &:not(:first-child) {
            a {
                margin-top: 23px;
                &::before {
                    content: '';
                    background-color: $brand-color-light-gray;
                    position: absolute;
                    left: 0;
                    top: -23px;
                    width: 87px;
                    height: 1px;
                    
                }
            }
        }
        a {
            padding-right: 35px;
            color: #fff;
            font-weight: $bold-weight;
            letter-spacing: 2.5px;
            text-transform: uppercase;
            position: relative;
            @include transition(all, 0.5s, ease, 0s);
            @extend %fontsize15;
            @include inline-block;
            
            &:hover {
                color: $brand-color;
                
                &::after {
                    right: 0;
                }
            }
            
            &::after {
                @include transition(all, 0.3s, ease, 0s);
                content: '';
                background-image: url('../images/icons/arrow-right.svg');
                background-repeat: no-repeat;
                background-size: contain;
                position: absolute;
                right: 6px;
                top: 1px;
                width: 14px;
                height: 20px;
            }
        }
    }
}

@include media-breakpoint-up(md) {
    .hero-visual-mit-buttons .landing-slide-tablet {
        display: block;
    }
    .landing-slide-mobile {
        display: none;
    }
    .landing-slider-content {
        h1 {
            transform: translateY(40%);
        }
    }
    .slider-contents {
        padding: 0 15px 0 ;
        bottom: 102px;
    }
    .slider-action-link {
        margin-top: 48px;
        li {
            @include inline-block();

            &:first-child {
                a {
                    margin-right: 66px;
                    margin-bottom: 0;
                }
            }

            &:not(:first-child) {
                a {
                    margin-left: 66px;
                    margin-top: 0;
                    &::before {
                        top: -24px;
                        left: -66px;
                        width: 1px;
                        height: 84px;
                    }
                }
            }
            a {
                margin-top: 4px;
                &::after {
                    top: 2px;
                }
            }
        }
    }
}

@include media-breakpoint-up(lg) { 
    .hero-visual-mit-buttons .landing-slide {
        display: block;
    }
    .hero-visual-mit-buttons .landing-slide-tablet {
        display: none;
    }
    .landing-slider-content {
        max-width: 841px;
        h1 {
            @include transition(transform, 0.5s, ease, 0s);
        }
    }
    .landing-slider-init {
        .landing-slide {
            @include transition(all, 0.5s, ease, 0s);
            min-height: 100vh !important;
            height: 100% !important;
        }
    }
    .slider-action-link {
        li {
            a {
                &::after {
                    top: 8px;
                }
            }
        }
    }
}