/*******************************************************************************
* All Slider
*******************************************************************************/
.landing-slider-init {
    overflow: hidden;
    display: block;
    height: 100vh;
    
    &.owl-loaded {
        height: auto;
        overflow: auto;
    }

    .landing-slide-tablet,
    .landing-slide-mobile,
    .landing-slide {
        position: relative;
        background-position: 0 30%;
        background-size: cover;
        background-repeat: no-repeat;
        @include transition(all, 0.2s, ease, 0s);
    }

    .button-slide {
        height: 100vh;
    }
}

.landing-slide {
    > .container{
        position: relative;
        width: 100%;
        height: 100%;
    }
}

.owl-carousel {    
    z-index: auto;
    .owl-dots {
        text-align: center;
        position: absolute;
        bottom: 30px;
        right: 30px;
        z-index: 999;
        button{
            width: auto;
        }
        .owl-dot {
            @include inline-block;
            span {
                width: 11.9px;
                height: 11.9px;
                margin: auto 5px;
                background: transparent;
                display: block;
                @include transition(opacity, 0.2s, ease, 0s);                
                border:2px solid #fff;
                border-radius: 50%;
            }
            &.active {
                span {
                    background: #fff;
                }
            }
        }
    }
}

.owl-carousel {
    .owl-nav {
        display: none;
        position: absolute;
        right: 0;
        top: 0;

        button {
            &.owl-next,
            &.owl-prev {
                width: 40px;
                height: 40px;
                padding: 13px 10px;
                position: relative;
                background-color: $brand-color;
                border: 1px solid $brand-color;
                @include transition(all, 0.5s, ease, 0s);

                &:hover, 
                &:focus {
                    background-color: transparent;
                }

                &::after {
                    content: '';
                    background-repeat: no-repeat;
                    background-size: contain;
                    width: 14px;
                    height: 11px;
                    @include horizontal-vertical-align($position: absolute);
                }
            }

            &.owl-next {
                &::after {
                    background-image: url('../images/icons/arrow-right-white.svg');                        
                }

                &:hover, 
                &:focus {
                    &::after {
                        background-image: url('../images/icons/arrow-right.svg');
                    }
                }
            }
            
            &.owl-prev {
                margin-right: 9px;

                &::after {
                    background-image: url('../images/icons/arrow-left-white.svg');
                }

                &:hover, 
                &:focus {
                    &::after {
                        background-image: url('../images/icons/arrow-left.svg');
                    }
                }
            }
        }
    }
}

.bg-yellow {
    .owl-carousel {
        .owl-nav {
            button {
                &.owl-next,
                &.owl-prev {
                    background-color: #fff;
                    border-color: #fff;

                    &:hover, 
                    &:focus {
                        background-color: transparent;
                    }
                }
                &.owl-next {
                    &::after {
                        background-image: url('../images/icons/arrow-right.svg');                        
                    }
    
                    &:hover, 
                    &:focus {
                        &::after {
                            background-image: url('../images/icons/arrow-right-white.svg');
                        }
                    }
                }
                
                &.owl-prev {
                    margin-right: 9px;
    
                    &::after {
                        background-image: url('../images/icons/arrow-left.svg');
                    }
    
                    &:hover, 
                    &:focus {
                        &::after {
                            background-image: url('../images/icons/arrow-left-white.svg');
                        }
                    }
                }
            }
        }
    }
}

@include media-breakpoint-up(md) {
    .owl-carousel {
        .owl-nav {
            @include inline-block();
        }
    }
}

@include media-breakpoint-up(lg) {
    .owl-carousel {   
        .owl-dots {
            bottom: 84px;
            right: 110px;
        }
    }
}

/* Landscape */
@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2) and (orientation: landscape) { 
    .landing-slider-init{
        .landing-slide{
            min-height:415px;
        }
    }
}