.news-list-view > .row > .col-sm-12 {
    .event-teaser-thumb {
        border-bottom: 1px solid $brand-color-light-gray;
    }

    &:last-child {
        .event-teaser-thumb {            
            border-bottom: 0;
        }
    }
}

.event-teaser-thumb {
    @include flexbox();
    @include align-items(center);
    @include flex-wrap(wrap);
    padding: 20px 0;
}

.event__postdescription p {
    margin-bottom: 0;
}

.event__button {
    display: block;
    margin-left: auto;
    min-width: 170px;
    
    a {
        display: block;
    }
}

.event-list-date {
    @extend %fontsize32;
    min-width: 140px;
}

.event__posttitle {
    font-weight: $regular-weight;
    margin: 10px 40px 10px 0;
}

.event-list-date,
.event__postdescription {
    margin-right: 30px;
}

@include media-breakpoint-up(lg) {
    .news-list-view > .row > .col-sm-12 {
        .event-teaser-thumb {
            border-bottom: 0;
        }
    }

    .event-teaser-thumb {
        @include flex-wrap(nowrap);
        padding: 38px 0;
    }

    .event__button {
        margin-left: 0;
    }

    .event__posttitle {
        margin: 0 60px 0 0;
    }

    .event-list-date,
    .event__postdescription {
        margin-right: 40px;
    }
}
