/*******************************************************************************
* Testimonial Text
*******************************************************************************/
.testimonial-section {
    background-color: $brand-color;
}

.testimonial {
    padding: 40px 0;
    margin: 0 auto;
    color: #fff;
    max-width: 960px;
    .sub-tag {
        color: inherit;
        margin-bottom: 8px;
    }
}

.testimonial-zitat {
    margin: 20px 0;
    padding: 25px 0 27px;
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    color: inherit;
    position: relative;

    h5 {
        color: inherit;
    }
    
    &:before, &:after {
        content: '„';
        color: inherit;
        font-size: 83px;
        line-height: 0;
        background-color: $brand-color;
        position: absolute;
        top: -25px;
        left: 50%;
        height: 40px;
        padding: 0 10px;
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
    }

    &:after {
        bottom: -15px;
        top: auto;
    }
}

.profile-block {
    padding: 15px 0;
}

.profile-image {
    display: flex;
    padding-right: 30px;
    padding-bottom: 30px;
    position: relative;

    &:before {
        content: '';
        border: 1px solid #fff;
        border-radius: 0;
        border-bottom-left-radius: 40px;
        width: 120px;
        height: 121px;
        position: absolute;
        left: 20px;
        top: 25px;
        z-index: 1;
        @include rotate(-30deg);
    }

    .profile-image-frame {
        border-bottom-left-radius: 40px;
        margin-left: 27px;
        margin-top: 24px;
        height: 116px;
        width: 120px;
        overflow: hidden;
        @include rotate(-20deg);
        position: relative;
    }

    .image {
        height: 154px;
        width: 154px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: absolute;
        left: -16px;
        top: -18px;
        @include rotate(20deg);
    }
}
.profile-content {
    max-width: 450px;

    p.profile-name {
        margin-bottom: 0px;
        font-weight: $bold-weight;
    }
}
@include media-breakpoint-up(sm) {
    .testimonial-zitat {
        padding: 40px 0 45px;
        
        &:before, &:after {
            font-size: 110px;
            top: -35px;
            height: 60px;
            padding: 0 4px 0 10px;
        }
        &:after {
            bottom: -28px;
            top: auto;
        }
    }
    .profile-block {
        display: flex;
    }
    .profile-content {
        padding-left: 18px;
        padding-right: 12px;
        padding-top: 8px;
    }
}
@include media-breakpoint-up(lg) {
    .profile-image {
        &:before {
            width: 153px;
            height: 153px;
            left: 26px;
            top: 27px;
        }

        .profile-image-frame {
            margin-left: 37px;
            margin-top: 24px;
            height: 150px;
            width: 150px;
        }

        .image {
            height: 200px;
            width: 200px;
            left: -25px;
            top: -25px;
        }
    }
}
@include media-breakpoint-up(xl) {
    .testimonial {
        padding: 60px 0;
    }
}

@include media-breakpoint-up(xxl) {
    .testimonial {
        padding: 80px 0;
    }
}
