/*******************************************************************************
* 404 Page Style
*******************************************************************************/
.error-text {
    h2 {
        font-size: 80px;
        line-height: normal;
        margin: 0;
    }

    h3 {
        margin-bottom: 15px;
        color: $body-color;
    }
}

@include media-breakpoint-up(md) {  
    .error-text {
        h2 {
            font-size: 130px;
        }
    }
}

@include media-breakpoint-up(lg) {  
    .error-text {
        h2 {
            font-size: 180px;
        }
    }
}